import React, { useEffect, useState } from 'react';
import MenuIcon from '@material-ui/icons/Menu';
import { useTheme } from '@material-ui/core/styles';
import BuyAxionAuctionModal from './BuyAxionAuctionModal';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { Typography, AppBar, Toolbar, IconButton, Grid, Menu, MenuItem, Button } from '@material-ui/core';

import Brightness5Icon from '@material-ui/icons/Brightness5';
import Brightness2Icon from '@material-ui/icons/Brightness2';

const DARK_MODE_KEY = "AxionStatsDark";

const Header = () => {
    const [anchorEl, setAnchorEl] = useState(null);
    const [modalOpen, setModalOpen] = useState(false);
    const [isDarkMode, setDarkMode] = useState(false);

    const theme = useTheme();
    const isSmall = useMediaQuery(theme.breakpoints.down('sm'));

    const toggleDarkMode = () => {
        const newDark = !isDarkMode;
        setDarkMode(newDark)
        localStorage.setItem(DARK_MODE_KEY, newDark)

        document.documentElement.style.setProperty("--main-bg-color", (newDark ? '#232839' : '#EAEEF7'));
        document.documentElement.style.setProperty("--primary-main-color", (newDark ? 'rgb(239 232 255)' : '#4c598e'));
        document.documentElement.style.setProperty("--text-secondary-color", (newDark ? 'rgb(239 232 255)' : 'rgba(0,0,0, 0.54)'));
    }

    const openWebsite = () => window.open("https://axion.network", "_blank");
    const openPortal = () => window.open("https://stake.axion.network/staking?ref=0xbe42d298d31b2551ae9e6e88b838a3ba5dc1d6cd", "_blank");

    const openReferral = () => {
        setModalOpen(true);
        setAnchorEl(null)
    }

    const handleClick = (event) => { setAnchorEl(event.currentTarget) }
    const handleClose = () => { setAnchorEl(null) }

    const addToMM = () => {
        if(window.ethereum) {
            window.ethereum.sendAsync({
                method: 'wallet_watchAsset',
                id: Math.round(Math.random() * 10000),
                params: {
                    'type': 'ERC20',
                    'options': {
                        'address': "0x7D85e23014F84E6E21d5663aCD8751bEF3562352",
                        'symbol': "AXN",
                        'decimals': 18,
                        'image': 'https://i.imgur.com/MdKlJfl.png',
                    },
                }
            })
        } else
            alert(`There was an error communicating with Metamask. ${isSmall ? "If you are not using the built-in MetaMask browser, please switch to that." : ""}`)
    }

    return (
        <div>
            <AppBar position="static" className="navBar" elevation={10} >
                <Toolbar>
                    {/* {!isSmall && (
                        <div style={{ cursor: 'pointer', position: 'absolute', right: 12 }} onClick={toggleDarkMode}>
                            {isDarkMode ? <Brightness5Icon /> : <Brightness2Icon />}
                        </div>
                    )} */}

                    <Grid container justify="space-between" alignItems="center">
                        <Grid item>
                            <Typography variant="h5" align="center" style={{ fontWeight: '100', color: "#FFF" }}>Axion Stats</Typography>
                        </Grid>
                        <Grid item>
                            {!isSmall && (
                                <div className="headerLinks">
                                    <Typography onClick={openWebsite} variant="subtitle2" style={{ color: "#FFF" }} display="inline" className="clickableLink noselect">Axion Website</Typography>
                                    <Typography onClick={openPortal} variant="subtitle2" style={{ color: "#FFF" }} display="inline" className="clickableLink noselect">Staking Portal</Typography>
                                    <Typography onClick={addToMM} variant="subtitle2" style={{ color: "#FFF" }} display="inline" className="clickableLink noselect">Add to MetaMask</Typography>

                                    <Button onClick={openReferral} variant="contained" color="secondary" className="referralButton">
                                        <Typography variant="subtitle2">Buy Axion, Get 10% Bonus</Typography>
                                    </Button>
                                </div>
                            )}

                            {isSmall && (<>
                                <IconButton edge="start" color="inherit" onClick={handleClick}>
                                    <MenuIcon />
                                </IconButton>

                                <Menu anchorEl={anchorEl} keepMounted open={Boolean(anchorEl)} onClose={handleClose} style={{padding: 0, margin: 0}}>
                                    <MenuItem onClick={openWebsite}>Axion Website</MenuItem>
                                    <MenuItem onClick={openPortal}>Staking Portal</MenuItem>
                                    <MenuItem onClick={addToMM}>Add to MetaMask</MenuItem>
                                    <MenuItem onClick={openReferral}>Buy Axion - 10% Bonus</MenuItem>
                                </Menu>
                            </>)}
                        </Grid>
                    </Grid>

                </Toolbar>
            </AppBar>

            <BuyAxionAuctionModal isOpen={modalOpen} close={() => setModalOpen(false)}/>
        </div>
    )
}

export default Header;