import React from 'react';
import moment from 'moment';
import StatCard from './StakingStatCard';
import { Grid, Typography } from '@material-ui/core';

const StakingStats = ({ stakingData, loading, supply }) => {

    return (
        <div style={{ padding: '2%', paddingBottom: 0 }}>
            <Typography variant="h4" align="center" color="primary" style={{ fontWeight: '100' }}>Staking Stats</Typography>
            {stakingData.timestamp && (
                <>
                    <Typography variant="subtitle2" color="textSecondary" style={{ fontWeight: '400' }} align="center">Data Last Refreshed: {moment(stakingData.timestamp).format("h:mm a")}</Typography>
                    <Typography variant="subtitle2" color="textSecondary" style={{ fontWeight: '400' }} align="center">Data as of Block: {stakingData.block}</Typography>
                </>
            )}
          
            <div style={{ margin: '2%' }}>
                <Grid container spacing={3} justify="center">
                    <Grid item lg={4} md={6} xs={10}>
                        <StatCard stat="Total Active Stakes" amount={stakingData?.total_active_stakes ?? 0} suffix="" loading={loading} />
                    </Grid>
                    <Grid item lg={4} md={6} xs={10}>
                        <StatCard stat="Total Axion Staked" amount={Math.round(stakingData?.total_axn_staked ?? 0)} suffix="AXN" loading={loading} />
                    </Grid>
                    <Grid item lg={4} md={6} xs={10}>
                        <StatCard stat="Percentage Staked" amount={(stakingData?.total_axn_staked / (stakingData?.total_axn_staked + supply)).toLocaleString('en-US', { style: 'percent', minimumFractionDigits: 2 })} suffix="" loading={loading} />
                    </Grid>
                    <Grid item lg={4} md={6} xs={10}>
                        <StatCard stat="Average Stake Length" amount={Math.round(stakingData?.avg_days ?? 0)} suffix="Days" loading={loading} />
                    </Grid>
                    <Grid item lg={4} md={6} xs={10}>
                        <StatCard stat="Average Stake Amount" amount={Math.round(stakingData?.avg_axn ?? 0)} suffix="AXN" loading={loading} />
                    </Grid>   
                    <Grid item lg={4} md={6} xs={10}>
                        <StatCard stat="Total 5,555 Club Members" amount={stakingData?.total_active_stakes_5555 ?? 0} suffix="" loading={loading} bracketStat={Math.round(stakingData?.total_axn_staked_5555 ?? 0)} bracketStatSuffix="AXN" tooltip="Requires at least 2.5M AXN staked for 5555 days"/>
                    </Grid>
                </Grid>
            </div>
        </div>
    )
}

export default StakingStats;